@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap");
@import url("https://cdn-uicons.flaticon.com/uicons-regular-straight/css/uicons-regular-straight.css");
@import url('https://cdn-uicons.flaticon.com/uicons-bold-rounded/css/uicons-bold-rounded.css');

// colors
$mainColor : #F3F1F5;
$headerColor : #BFA2DB;
$footerColor : #F0D9FF;
$quColor : #d9b3f2;
$qlColor : #f7c5fd;
$contentColor : #EDFCF1;
$textColor : #7F7C82;
$alTextColor : #BFA2DB;
$white : #ffffff;

* {
    padding: 0;
    margin: 0;
    font-family: "JetBrains Mono", monospace;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

html, body
{
    height: 100%;
    min-height: 100%;
}

.center-items {
    display: flex;
    justify-content: center;
    align-items: center;
}

.center-items-inline {
    display: flex;
    align-items: center;
}

.flex-row {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.flex-column {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.custom-scrollbar {
    &::-webkit-scrollbar {
        width: 5px;
        height: 10px;
        margin-left: 10px;
    }

    &::-webkit-scrollbar-thumb {
        display: none;
    }
}

.shadow {
    -webkit-box-shadow: 0px 7px 10px 4px rgba(0,0,0,0.3);
    -moz-box-shadow: 0px 7px 10px 4px rgba(0,0,0,0.3);
    box-shadow: 0px 7px 10px 4px rgba(0,0,0,0.3);
}

.flex-space-center-wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.padding-content {
    padding: 1vh 2vw;
    box-sizing: border-box;
}

.input_group {
    position: relative;
    width: 30vw;

    input {
        width: 100%;
        height: auto;
        padding: 1vh 0.5vh;
        box-sizing: border-box;
        border: 1px solid #000;
        background-color: $mainColor;
        color: $textColor;
    }

    i {
        width: 3vh;
        height: 3vh;
        position: absolute;
        right: 0;
        top: 1.2vh;
        opacity: 0;
        transition: all 0.2s;
    }

    &.success {
        input {
            border-color:rgba(0, 240, 0, 0.5);
        }
        i.fi-rs-check {
            opacity: 1;
            color:rgba(0, 240, 0, 0.9);
        }
    }

    &.error {
        input {
            border-color:rgba(240, 0, 0, 0.9);
        }
        i.fi-rs-exclamation {
            opacity: 1;
            color:rgba(240, 0, 0, 0.9);
        }
        &.message .error_message {
            height: auto;
        }
        &.type_err .error_type {
            height: auto;
        }
    }

    .error_message,
    .error_type {
        font-size: 0.9em;
        color: #EE3F3F;
        font-weight: bold;
        overflow: hidden;
        height: 0;
    }

    input:focus + .field_name, input:valid + .field_name {
        font-size: .8em;
        color: #afbdcf;
        top: -0.75vh;
        left:2vh;
        background: $mainColor;
        padding: 0px 5px 0px 5px;
    }

    .field_name {
        position: absolute;
        font-size: 1em;
        top: 1vh;
        left: 0.5vh;
        pointer-events: none;
        transition:0.2s ease all;
    }

    input:focus {
        outline:none;
    }
}

.padding-lr {
    padding: 1vh 2vh;
    box-sizing: border-box;
}

.hide {
    display: none;
}

body {
	background-color: #FF3CAC;
	background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%);


	header {
		position: relative;
		height: 6vh;
		padding: 0.5vh 2vw;
		box-sizing: border-box;

		.logo {
			width: 5vh;
			height: 5vh;
			background-color: white;
		}

		.title {
			height: 100%;
			padding: 0 1.5vw;
		}

		nav {
			position: absolute;
			right: 2vw;
			display: flex;
			
			.btn_contanier {
				height: 5vh;
				.btn {
					position: relative;
					width: 5vh;
					height: 5vh;
					background-color: rgba(0, 0, 0, 0);
					border: none !important;
	
					span {
						i::before {
							font-size: 2.5vh;
						}
					}
				}
				.capture {
					width: 0;
					overflow-x: hidden;
					transition: width 0.5s;
				}

				.btn:hover + .capture {
					width: 100%;
				}
			}
		}
	}
}
